@import url(https://db.onlinewebfonts.com/c/f4d1593471d222ddebd973210265762a?family=Pokemon);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(132, 196, 238);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "Pokemon";
  src: url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.eot");
  src: url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.eot?#iefix")
      format("embedded-opentype"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.woff2")
      format("woff2"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.woff")
      format("woff"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.ttf")
      format("truetype"),
    url("https://db.onlinewebfonts.com/t/f4d1593471d222ddebd973210265762a.svg#Pokemon")
      format("svg");
}
.App {
  font-family: Pokemon;
  text-align: center;
  letter-spacing: 2px;
  background-color: rgb(132, 196, 238);
  min-height: 100vh;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

html {
  overflow: hidden;
}
ul {
  list-style: none;
}
h1 {
  color: yellow;
  text-shadow: 5px 5px 5px blue;
  background-color: tomato;
  margin: 0;
}
/* * {
  background-color: rgb(132, 196, 238);
} */
.search-input {
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  -webkit-align-items: center;
          align-items: center;
  border: none;
  background-color: wheat;
  border-radius: 10px;
  height: 30px;
  width: 30%;
}
.search-button {
  width: 3%;
  cursor: pointer;
  margin-left: 1%;
}
.input-btn {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.catch-release-btn {
  height: 3em;
  cursor: pointer;
  transition: -webkit-transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;
}
.catch-release-btn:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.img-detail {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.pokemon-img {
  height: 5em;
}
.div-pokemon-details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  -webkit-justify-content: start;
          justify-content: start;
}

.pokemon-collection-item,
button,
span {
  cursor: pointer;
}

.main {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.collection-div {
  display: inline-block;
  width: 17%;
  max-width: 17%;
}

.ul-pokemon-types {
  display: grid;
  margin: auto;
  padding: 0%;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  grid-template-columns: 20% 20% 20% 20% 20% 20% 20%;
  grid-template-rows: -webkit-max-content;
  grid-template-rows: max-content;
  grid-column-gap: 1.5%;
  -webkit-column-gap: 1.5%;
          column-gap: 1.5%;
  grid-row-gap: 1%;
  row-gap: 1%;
  max-width: -webkit-min-content;
  max-width: min-content;
}

.li-pokemon-details {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.div-pokemon-details {
  text-align: start;
}
.next-btn,
.get-collection-btn {
  font-family: pokemon;
  letter-spacing: 2px;
  color: yellow;
  text-shadow: 5px 5px 5px blue;
  background-color: tomato;
}

/*spinner css*/
.spinner-div {
  display: none;
}

.blur {
  -webkit-filter: blur(8px);
          filter: blur(8px);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

.loader {
  position: fixed;
  -webkit-align-content: center;
          align-content: center;
  display: -webkit-flex;
  display: flex;
  z-index: 1;
  left: 50%;
  top: 50%;
}
.loader div {
  -webkit-animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: loader 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  -webkit-transform-origin: 40px 40px;
          transform-origin: 40px 40px;
}
.loader div:after {
  content: " ";
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgb(64, 54, 100);
  margin: -4px 0 0 -4px;
}
.loader div:nth-child(1) {
  -webkit-animation-delay: -0.036s;
          animation-delay: -0.036s;
}
.loader div:nth-child(1):after {
  top: 63px;
  left: 63px;
}
.loader div:nth-child(2) {
  -webkit-animation-delay: -0.072s;
          animation-delay: -0.072s;
}
.loader div:nth-child(2):after {
  top: 68px;
  left: 56px;
}
.loader div:nth-child(3) {
  -webkit-animation-delay: -0.108s;
          animation-delay: -0.108s;
}
.loader div:nth-child(3):after {
  top: 71px;
  left: 48px;
}
.loader div:nth-child(4) {
  -webkit-animation-delay: -0.144s;
          animation-delay: -0.144s;
}
.loader div:nth-child(4):after {
  top: 72px;
  left: 40px;
}
.loader div:nth-child(5) {
  -webkit-animation-delay: -0.18s;
          animation-delay: -0.18s;
}
.loader div:nth-child(5):after {
  top: 71px;
  left: 32px;
}
.loader div:nth-child(6) {
  -webkit-animation-delay: -0.216s;
          animation-delay: -0.216s;
}
.loader div:nth-child(6):after {
  top: 68px;
  left: 24px;
}
.loader div:nth-child(7) {
  -webkit-animation-delay: -0.252s;
          animation-delay: -0.252s;
}
.loader div:nth-child(7):after {
  top: 63px;
  left: 17px;
}
.loader div:nth-child(8) {
  -webkit-animation-delay: -0.288s;
          animation-delay: -0.288s;
}
.loader div:nth-child(8):after {
  top: 56px;
  left: 12px;
}
@-webkit-keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

